* {
  margin: auto;
  padding: 0;
}
body {
  height: 130vh;
  background-color: pink !important;
}
body::-webkit-scrollbar {
  display: none;
}
.heading {
  text-align: center;
}
h1 {
  font-weight: 900 !important;
  background: transparent !important;
  color: rgb(255, 95, 207);
}
hr {
  background-color: rgb(255, 95, 207);
  height: 5px !important;
}
.row {
  margin-bottom: 1rem;
}
h3 {
  color: rgb(255, 95, 207);
  font-weight: 800 !important;
  font-size: 2em !important;
}
.card {
  padding: 10px;
  width: 800px;
  box-shadow: 10px;
  box-shadow: 1px 1px 10px 0.25px rgb(0, 0, 0);
}
img {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  border: 4px solid pink !important;
}
.btn {
  color: white !important;
  background-color: rgb(255, 95, 207) !important;
}
